стен другому, пока не завершится общий цикл. В последовательной игре каждый игрок владеет информацией о предыдущем ходе своего оппонента до того, как сделать свой выбор. ")])]),_c('div',{staticClass:"image"},[_c('v-img',{attrs:{"width":"600","src":require("./assets/3_tip.png")}})],1),_c('div',{staticClass:"wrapper"},[_c('div',{staticClass:"image"},[_c('v-img',{attrs:{"width":"600","src":require("./assets/4_tip.png")}})],1),_c('div',{staticClass:"text"},[_c('p',[_vm._v(" С полной или неполной информацией "),_c('br'),_vm._v(" Эти типы являются подвидом последовательных игр, и названия их говорят сами за себя. ")])])])])]),_c('div',{staticClass:"wrapper",staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v("ПРИМЕНЕНИЕ ТЕОРИКО-ИГРОВЫХ МОДЕЛЕЙ")]),_vm._v(" "),_c('br'),_c('p',[_vm._v(" Теория игр находит свое применение во всех областях социальных наук, а также в логике, экономике, системной науке и информатике. Например: СПИСОК (социология) теория игр рассматривает человеческое взаимодействие именно как игру; (логика) та самая дилемма заключенного, рассмотренная ранее, и много ей подобных логических игр; (экономика) рыночная конкуренция; (экология) загрязнение окружающей среды; (информатика) разработка компьютерных приложений (в том числе и игр), оптимизация программы, обучение нейронных сетей. Последний вариант нас интересует больше, вот и рассмотрим его подробнее. ")]),_c('div',{staticClass:"image"},[_c('v-img',{attrs:{"width":"600","src":require("./assets/5_tip.png")}})],1),_c('div',{staticClass:"image"},[_c('v-img',{attrs:{"width":"600","src":require("./assets/6_tip.png")}})],1)]),_c('div',{staticStyle:{"margin":"10px"}},[_c('br'),_c('h2',[_vm._v("ИГРА В ИНФОРМАЦИОННЫХ СИСТЕМАХ")]),_c('br'),_c('p',[_vm._v(" Сильно востребованы исследования в сфере компьютерных технологий, например анализ аукционов, которые проводятся компьютерами в автоматическом режиме. Кроме того, теория игр сегодня позволяет еще раз задуматься над тем, как работают компьютеры, каким образом строится кооперация между ними. Скажем, серверы в сети можно рассматривать как игроков, которые пытаются скоординировать свои действия/ ")])]),_c('div',{staticClass:"wrapper",staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v("КОМПЬЮТЕРНЫЕ ИГРЫ")]),_c('br'),_c('div',{staticClass:"text"},[_c('p',[_vm._v(" Первое что приходит на ум при упоминании теории игр в информационных технологиях – это компьютерные игры или иначе «видеоигры». Но тут не все так просто. "),_c('br'),_vm._v(" Данный раздел науки был создан, когда компьютерные игры пребывали в зачаточном состоянии, и поэтому имеет отношение к ним очень слабое. В классической теории игр рассматриваются лишь математические модели, и за названием раздела скрывается всего лишь раздел теории вероятностей для игровых нужд. Для изучения феномена компьютерных игр одной математики явно недостаточно. "),_c('br'),_vm._v(" Лишь совсем недавно появилась соответствующая наука, которая занимается всеми вопросами, связанными с компьютерными играми. "),_c('br'),_vm._v(" Людология – наука, занимающаяся исследованиями игр как современной формы коммуникации и творчества. ")])]),_c('div',{staticClass:"image"},[_c('v-img',{attrs:{"width":"600","src":require("./assets/7_tip.png")}})],1)]),_c('div',{staticClass:"wrapper",staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v("СЕТЕВАЯ АРХИТЕКТУРА")]),_c('p',[_vm._v(" Сетевая архитектура - это проектирование компьютерной сети. Это основа для спецификации физических компонентов сети, их функциональной организации и конфигурации, принципов ее работы и процедур, а также используемых протоколов связи. Теория игр широко используется в исследованиях беспроводных сетей для понимания точек стабильной работы сетей, состоящих из автономных/самостоятельных узлов. Узлы рассматриваются как игроки. Служебные функции часто выбираются в соответствии с достигнутой скоростью соединения или аналогичными техническими показателями. Исследования, проведенные в этом контексте, могут быть сгруппированы следующим образом: "),_c('br')]),_c('div',{staticClass:"image"},[_c('v-img',{attrs:{"width":"600","src":require("./assets/8_tip.png")}})],1),_c('div',{staticClass:"image"},[_c('v-img',{attrs:{"width":"500","src":require("./assets/9_tip.png")}})],1)]),_c('div',{staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v("ТЕОРИЯ ИГР КАК МЕХАНИЗМ ДЛЯ АНАЛИЗА КРУПНОМАСШТАБНЫХ ДАННЫХ")]),_c('p',[_vm._v(" Метод PCA как равновесие Нэша. Анализ главных компонентов (PCA) – популярный метод анализа больших наборов данных, содержащих большое количество измерений / признаков на одно наблюдение. "),_c('br'),_vm._v(" Равновесие Нэша – концепция решения, одно из ключевых понятий теории игр. Так называется набор стратегий в игре для двух и более игроков, в котором ни один участник не может увеличить выигрыш, изменив свою стратегию, если другие участники своих стратегий не меняют. "),_c('br'),_vm._v(" В настоящее время PCA подход повсеместно используется в качестве средства, обеспечивающего первый этап работы конвейера обработки данных и позволяющего с лёгкостью визуализировать данные. Со времени изобретения метода PCA прошло уже более века, но до сей поры он не утратил своей актуальности. "),_c('br'),_vm._v(" 1. Раньше данные заносились вручную в бумажные блокноты, их было не так много, и их обработка не представляла сложности, а сейчас они хранятся в центрах обработки данных огромных размеров, и применение метода требует колоссальных вычислительных ресурсов. Исследователи пытались применять рандомизированные алгоритмы и другие методы, повышающие качество масштабирования метода PCA, однако, как удалось выяснить, такие подходы практически неприменимы к массивным наборам данных. "),_c('br'),_vm._v(" 2. Метод PCA имеет много общего с множеством важных инженерных методов и алгоритмов машинного обучения, в частности с методом разложения по сингулярным значениям (SVD). Благодаря правильно выбранному подходу к применению метода PCA данные алгоритмы стали широко применяться во всех областях машинного обучения. ")])]),_c('div',{staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v("ИСКУССТВЕННЫЕ НЕЙРОННЫЕ СЕТИ")]),_c('div',[_c('p',[_vm._v(" Искусственная нейронная сеть — математическая модель, а также ее программное или аппаратное воплощение, построенная по принципу организации и функционирования биологических нейронных сетей — сетей нервных клеток живого организма. "),_c('br'),_vm._v(" Нейронные сети не программируются в привычном смысле этого слова, они обучаются. Возможность обучения — одно из главных преимуществ нейронных сетей перед традиционными алгоритмами. Технически обучение заключается в нахождении коэффициентов связей между нейронами. В процессе обучения нейронная сеть способна выявлять сложные зависимости между входными данными и выходными, а также выполнять обобщение. Это значит, что в случае успешного обучения сеть сможет вернуть верный результат на основании данных, которые отсутствовали в обучающей выборке, а также неполных и/или «зашумленных», частично искажённых данных. ")])]),_c('div',[_c('v-img',{staticStyle:{"margin":"auto"},attrs:{"width":"600","src":require("./assets/10_tip.png")}})],1)]),_c('div',{staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v("НЕЙРОННЫЕ СЕТИ И ТЕОРИЯ ИГР")]),_c('div',[_c('p',[_vm._v(" Генетический алгоритм обучения По своему принципу он схож с эволюционными процессами природы, которые основываются на комбинировании (скрещивании) результатов. "),_c('br'),_vm._v(" Другими словами, происходит естественный отбор, где новое поколение является продуктом комбинации результатов с самыми лучшими свойствами. Если итог такого скрещивания не подходит по каким-то критериям, то отбор совершается вновь, пока продукт не станет совершенным. "),_c('br'),_vm._v(" Приведем аналогию на примере обучения Яндекс-роботов: Изначально конструируется много роботов для исследования работы нейронной сети. Стоит заметить, что в этот момент они обучаются независимо друг от друга. Далее создается выборка по нескольким критериям (координация в пространстве, минимум дтп и так далее), и «мозги» этих роботов (обычно 5-10 штук) усредняются и клонируются в новое поколение роботов. И так доводя до идеала. ")])])]),_c('div',[_c('v-img',{staticStyle:{"margin":"auto"},attrs:{"width":"600","src":require("./assets/11_tip.png")}})],1),_c('div',[_c('p',[_vm._v(" Причем же здесь теория игр? Можно отследить ее признаки: Больше одного игрока (выборка из 1000 роботов); Имеется цель и стратегия(доставить заказ по определенному маршруту без столкновений) Есть победители и проигравшие (доставили – не доставили). ")])]),_c('div',{staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v(" ВЫВОД")]),_c('p',[_vm._v(" Таким образом в ходе выполнения данной работы мне удалось достигнуть поставленной цели и выполнить все необходимые для этого задачи, а то есть: изучить концепцию теорико-игровых моделей в информационных системах, а конкретно: дала определения основным понятиям темы; обозначила сферы применения теории игр; проанализировала влияние теорико-игровых моделей на информационную систему. ")])]),_c('div',{staticStyle:{"margin":"10px"}},[_c('h2',[_vm._v("ЛИТЕРАТУРА")]),_c('p',[_vm._v(" Нейман Дж. фон, Моргенштерн О. Теория игр и экономическое поведение. — М.: Наука, 1970. — 983 с. "),_c('br'),_vm._v(" Васин А. А., Морозов В. В. Теория игр и модели математической экономики. — М.: МГУ, 2005. — 272 с. "),_c('br'),_vm._v(" Позиционные игры / ред. Воробьёв Н. Н., Врублевская И. Н.. — Москва: Наука, 1967. — 522 с. "),_c('br'),_vm._v(" Язов Ю.К. Сердечный A.Л., Шаров И.A. Методический подходов к оцениванию эффективности ложных информационных систем. 2014. с. 55-60. "),_c('br'),_vm._v(" Горбань А.Н. Обучение нейронных сетей. — М.: СССР-США СП «Параграф», 1990. — 160 с. "),_c('br'),_vm._v(" Ерёмин Д. М., Гарцеев И. Б. Искусственные нейронные сети в интеллектуальных системах управления. — М.: МИРЭА, 2004. — 75 с. "),_c('br'),_vm._v(" Теория игр [Электронный ресурс]. – URL: https://habr.com/ru/post/163681/ (дата обращения: 29.11.2022) "),_c('br'),_vm._v(" Теория игр как механизм для анализа кру